* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: Arial, Helvetica, sans-serif;
}

.container {
  width: 100%;
  max-width: 1200px;
  margin: 30px auto;
  padding: 15px;
}

.clearfix {
  clear: both;
  content: "";
  display: table;
}

h2 {
  font-size: 24px;
  font-weight: 600;
  margin-bottom: 30px;
}

.profileContainer {
  float: left;
  width: 25%;
  padding: 15px;
}

.repoListContainer {
  float: right;
  width: 75%;
  padding: 15px;
}

.userAvatar {
  width: 260px;
  height: auto;
  max-height: 260px;
  object-fit: cover;
  border-radius: 50%;
  max-width: 100%;
}

.userName {
  font-weight: 300;
  font-size: 18px;
  color: #6c757f;
  margin-bottom: 15px;
}

.userBio {
  margin-bottom: 15px;
}

.userFollows {
  font-size: 12px;
  color: #6c757f;
  margin-bottom: 15px;
}

.userFollows b {
  color: #222;
}

.userLocation {
  font-size: 15px;
}

ul {
  padding: 5px;
  list-style: none;
}

li {
  padding: 30px 0px;
  border-bottom: 1px solid #ddd;
}

a {
  text-decoration: none;
  color: #0366d6;
  font-weight: 500;
  font-size: 20px;
  display: block;
  margin-bottom: 15px;
}

a:hover {
  text-decoration: underline;
}

.repoLanguage {
  font-size: 14px;
  color: #6c757f;
  font-weight: 500;
  padding: 0px 15px 0px 0px;
}

.repoLanguage::before {
  display: inline-block;
  content: "";
  border-radius: 50%;
  background-color: green;
  width: 10px;
  height: 10px;
  margin-right: 5px;
}

.repoUpdated {
  font-size: 14px;
  color: #6c757f;
}

input {
  padding: 8px;
  border: none;
  outline: none;

  border: 1px solid #ddd;
  border-radius: 6px;
  display: block;
  width: 100%;
}

input:active,
input:focus {
  border-width: 2px;
  border-color: #13ade1;
  margin-bottom: -2px;
}

.paginate {
  text-align: center;
}

.page-btn {
  border: none;
  outline: none;
  background-color: #f9f9f9;
  border: 1px #ddd solid;
  padding: 8px 16px;
  border-radius: 8px;
  font-weight: bold;
  color: #0366d6;
}

.page-btn:hover {
  cursor: pointer;
  background-color: #0366d6;
  color: #fff;
}

.page-btn:first-child {
  border-right: none;
  border-top-right-radius: 0px;
  border-bottom-right-radius: 0px;
}

.page-btn:last-child {
  border-top-left-radius: 0px;
  border-bottom-left-radius: 0px;
}
